import vm from "@/main.js";
export function listMsg() {
  const res = {
    code: 200,
    data: Array.from(vm.$store.state.messageList),
  };
  return Promise.resolve(res);
}
export function sendMsg(msg) {
  const res = {
    code: 200,
    data: "发送成功",
  };
  vm.$store.commit("addMessage", msg);
  return Promise.resolve(res);
}
