<template>
  <div class="top-bar">
    <div class="bodyContent body-flex">
      <!-- 聊天框 开始 -->
      <div style="flex: 1"></div>
      <el-card style="width: 20%; padding: 0">
        <ul>
          <li
            @click="chatWith(item)"
            class="user-list-item"
            :class="{ checked: checkedIndex === index }"
            v-for="(item, index) in userList"
          >
            {{ item.username }}
          </li>
        </ul>
      </el-card>
      <el-card>
        <div class="chat">
          <div class="chat-message-body">
            <!-- <Spin v-if="loading">
                <Icon class="icon" type="account" />
              </Spin> -->
            <div
              dis-hover
              v-for="(item, index) in data"
              :key="index"
              class="message-card"
            >
              <div
                :class="
                  item.username == username
                    ? 'message-row-right'
                    : 'message-row-left'
                "
              >
                <!-- <img
                    :src="item.avatar ? item.avatar : defualtAvatar"
                    height="35"
                    width="35"
                  /> -->
                <div class="message-content">
                  <div
                    :style="
                      item.username == username
                        ? 'text-align:right;display: flex;flex-direction:row-reverse'
                        : ''
                    "
                  >
                    {{ item.username }}
                    <span class="message-time"> {{ item.createDate }}</span>
                  </div>
                  <div class="message-body">
                    {{ item.text }}
                    <!-- {{ item.text }} -->
                  </div>
                </div>
              </div>
            </div>
          </div>
          <el-input
            v-model="message.text"
            type="textarea"
            style="margin: 10px 0"
            placeholder="主动一点，世界会更大！"
            :rows="4"
          />
          <!-- <el-input
              v-model="content"
              type="textarea"
              style="margin: 10px 0"
              placeholder="主动一点，世界会更大！"
              :rows="4"
            /> -->
        </div>
        <div class="footer-btn">
          <el-button @click="sendMsg" type="primary">发送</el-button>
          <!-- <el-button @click="sendMessage" type="primary">发送</el-button> -->
        </div>
      </el-card>
      <div style="flex: 1"></div>
      <!-- 聊天框 结束 -->
    </div>
    <div class="footer"></div>
  </div>
</template>
<script>
import Icon, { types } from "@/components/Icon/index.vue";
import { listMsg, sendMsg } from "@/api/index";
export default {
  components: {
    Icon,
  },
  data() {
    return {
      stompClient: undefined,
      content: "",
      checkedIndex: "",
      data: [],
      username: localStorage.getItem("username"),
      interval: undefined,
      types,
      userList: [],
      message: {
        username: localStorage.getItem("username"),
        chatMessageId: 0,
        chatMessageType: "string",
        text: "",
        url: "string",
        createDate: "2022-12-01T02:17:03.481Z",
        senderId: 0,
        receiverId: 0,
        chatMessageStatus: 0,
      },
    };
  },
  methods: {
    chatWith(item) {
      this.$store.commit("updateUsername", item);
      this.checkedIndex = this.userList.findIndex((item2) =>
        Object.is(item, item2)
      );
      console.log("item:", item);
    },
    login() {
      console.log("=== login ===");
      this.$router.push("/login");
    },
    chat() {
      console.log("=== chat ===");
      this.$router.push("/chat");
    },
    getUserList() {
      console.log("=== getUserList ===");
      this.userList = [
        { username: "小明" },
        { username: "小红" },
        { username: "小兰" },
      ];
      //   http
      //     .get("/api/user", { params: { current: 0, size: 10 } })
      //     .then((res) => {
      //       console.log("userList:", res);
      //       this.userList = res;
      //     });
    },
    loadMsg() {
      listMsg().then((res) => {
        if (res.code == 200) {
          console.log("data:", this.data);
          this.data = res.data;
        }
      });
    },
    sendMsg() {
      this.data.push(this.message);
      console.log("store msgList:", this.$store.state.messageList);
      sendMsg({ ...this.message }).then((res) => {
        console.log("=== sendMsg res:", res);
      });
      //   http.post("/api/message/chat", this.message).then((res) => {
      //     console.log(res);
      //   });
    },

    connect() {
      try {
        const socket = new SockJS("/ws");
        this.stompClient = Stomp.over(socket);
        this.stompClient.connect(
          {
            token: localStorage.getItem("token"),
          },
          this.onConnected,
          this.onError
        );
      } catch (e) {
        console.log("err:", e);
      }
    },

    onConnected() {
      this.stompClient.subscribe(
        "/user/chat/" + localStorage.getItem("userid"),
        this.onMessageReceived
      );
      const chatMessage = {
        senderId: localStorage.getItem("userid"),
        receiverId: "4",
      };
      this.stompClient.send(
        "/chat.beginChatWithOne",
        {},
        JSON.stringify(chatMessage)
      );
    },

    onError(error) {
      console.log("error:");
      console.error(error);
    },

    sendMessage(event) {
      if (this.content && this.stompClient) {
        const chatMessage = {
          chatMessageType: "TEXT",
          senderId: localStorage.getItem("userid"),
          text: this.content,
          receiverId: "4",
        };
        console.log("----- ：" + chatMessage);

        this.stompClient.send(
          "/chat.sendTo0ne",
          {},
          JSON.stringify(chatMessage)
        );
      }
      event.preventDefault();
    },

    onMessageReceived(payload) {
      var message = JSON.parse(payload.body);
      console.log("recv msg:", message);

      if (message.type === "JOIN") {
        messageElement.classList.add("event-message");
        message.content = message.sender + " 上线~!";
      } else if (message.type === "LEAVE") {
        messageElement.classList.add("event-message");
        message.content = message.sender + " 离线了!";
      } else {
      }
    },
  },
  mounted() {
    console.log("mounted");
    this.loadMsg();
    this.interval = setInterval(() => {
      this.loadMsg();
    }, 2000);
    this.getUserList();
    // this.connect();
  },
  destroyed() {
    clearInterval(this.interval);
  },
};
</script>

<style lang="less">
@import "../components/styles/main.less";
.body-flex {
  display: flex;
  justify-content: space-around;
  .login-form,
  .reg-form {
    flex: 45%;
  }
}
</style>
<style lang="less">
.user-list-item {
  padding: 0 10px;
  width: 100%;
  height: 36px;
  line-height: 36px;
  text-align: left;
  border-bottom: 1px solid #f8f8f6;
  cursor: pointer;
}
.checked {
  background-color: #f8f8f6;
}
.message {
  height: 350px;
}
.ivu-card-body {
  padding: 5px;
}
.ivu-modal-body {
  padding: 0px 16px 16px 16px;
}
.chat-message-body {
  background-color: #f8f8f6;
  width: 545px;
  height: 350px;
  overflow: auto;
}
.message-card {
  margin: 5px;
}
.message-row-left {
  display: flex;
  flex-direction: row;
}
.message-row-right {
  display: flex;
  flex-direction: row-reverse;
}
.message-content {
  margin: -5px 5px 5px 5px;
  display: flex;
  flex-direction: column;
}
.message-body {
  border: 1px solid #d9dad9;
  padding: 5px;
  border-radius: 3px;
  background-color: #fff;
}
.message-time {
  margin: 0 5px;
  font-size: 5px;
  color: #d9dad9;
}
.footer-btn {
  float: right;
  margin-bottom: 5px;
}
</style>
